"use client";

import { Navigation } from "@/components/ui/navigation";
import { useState, useEffect } from "react";
import Link from "next/link";
import { useAuth } from "@/contexts/auth-context";
import { toast } from "sonner";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

interface BotMetrics {
  total_trades: number;
  winning_trades: number;
  losing_trades: number;
  total_profit: string;
  win_rate: number;
  current_drawdown: number;
}

interface MarketData {
  price: number;
  volume: number;
  timestamp: string;
}

export default function Dashboard() {
  const { user } = useAuth();
  const [botActive, setBotActive] = useState(true);
  const [metrics, setMetrics] = useState<BotMetrics | null>(null);
  const [marketData, setMarketData] = useState<MarketData[]>([]);
  const [activeStrategies, setActiveStrategies] = useState<string[]>([]);
  const [lastAnalysis, setLastAnalysis] = useState<any>(null);

  const handleBotToggle = async () => {
    try {
      const response = await fetch('/api/bot/toggle', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ active: !botActive })
      });

      if (!response.ok) throw new Error('Error al cambiar estado del bot');

      setBotActive(!botActive);
      toast[!botActive ? 'success' : 'warning'](
        !botActive ? 'Bot activado correctamente' : 'Bot desactivado',
        {
          description: !botActive 
            ? 'El bot comenzará a operar automáticamente' 
            : 'Las operaciones automáticas se han pausado',
          duration: 4000,
        }
      );
    } catch (error) {
      toast.error('Error al cambiar el estado del bot');
      console.error(error);
    }
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Obtener métricas del bot
        const metricsResponse = await fetch('/api/bot/metrics');
        const metricsData = await metricsResponse.json();
        setMetrics(metricsData);

        // Obtener datos de mercado
        const marketResponse = await fetch('/api/market/data');
        const marketData = await marketResponse.json();
        setMarketData(marketData);

        // Obtener estrategias activas
        const strategiesResponse = await fetch('/api/bot/strategies');
        const strategiesData = await strategiesResponse.json();
        setActiveStrategies(strategiesData.active);

        // Obtener último análisis
        const analysisResponse = await fetch('/api/market/analysis');
        const analysisData = await analysisResponse.json();
        setLastAnalysis(analysisData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
    const interval = setInterval(fetchData, 30000); // Actualizar cada 30 segundos

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="min-h-screen bg-black">
      <Navigation />
      
      <div className="p-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-cyan-400">
            Bienvenido, {user?.name || 'Usuario'}
          </h1>
          <p className="text-gray-300 mt-2">Vista general del trading automatizado</p>
        </div>

        {/* Estado General */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          {/* Estado del Bot */}
          <div className="bg-gray-900 p-6 rounded-xl shadow-md">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-semibold text-white">Estado Bot</h3>
              <button
                onClick={handleBotToggle}
                title={botActive ? "Haz clic para desactivar el bot" : "Haz clic para activar el bot"}
                aria-label={`Bot está ${botActive ? 'activo' : 'inactivo'}. Haz clic para ${botActive ? 'desactivar' : 'activar'}`}
                className={`group px-4 py-2 rounded-full text-sm font-medium transition-all duration-300 active:scale-95 flex items-center gap-2 ${
                  botActive 
                    ? "bg-green-600 hover:bg-green-700 shadow-lg shadow-green-600/20" 
                    : "bg-red-600 hover:bg-red-700 shadow-lg shadow-red-600/20"
                }`}
              >
                <div className={`w-2 h-2 rounded-full transition-all duration-300 ${
                  botActive 
                    ? 'bg-green-300 animate-pulse group-hover:bg-green-200' 
                    : 'bg-red-300 group-hover:bg-red-200'
                }`} />
                <span>{botActive ? "Activo" : "Inactivo"}</span>
              </button>
            </div>
            <p className="text-gray-400 text-sm">
              Última actividad: {lastAnalysis?.timestamp 
                ? new Date(lastAnalysis.timestamp).toLocaleString()
                : 'No disponible'}
            </p>
          </div>

          {/* Balance */}
          <div className="bg-gray-900 p-6 rounded-xl shadow-md">
            <h3 className="text-lg font-semibold text-white mb-2">Balance Total</h3>
            <p className="text-2xl font-bold text-green-400">
              ${metrics?.total_profit || '0.00'}
            </p>
            <p className="text-sm text-gray-400 mt-1">
              Win Rate: {metrics?.win_rate?.toFixed(2) || '0'}%
            </p>
          </div>

          {/* Operaciones */}
          <div className="bg-gray-900 p-6 rounded-xl shadow-md">
            <h3 className="text-lg font-semibold text-white mb-2">Operaciones</h3>
            <p className="text-2xl font-bold text-cyan-400">
              {metrics?.total_trades || 0}
            </p>
            <p className="text-sm text-gray-400 mt-1">
              {metrics?.winning_trades || 0} ganadas / {metrics?.losing_trades || 0} perdidas
            </p>
          </div>

          {/* Drawdown */}
          <div className="bg-gray-900 p-6 rounded-xl shadow-md">
            <h3 className="text-lg font-semibold text-white mb-2">Drawdown</h3>
            <p className="text-2xl font-bold text-green-400">
              {metrics?.current_drawdown?.toFixed(2) || '0'}%
            </p>
            <p className="text-sm text-gray-400 mt-1">Últimas 24h</p>
          </div>
        </div>

        {/* Gráfico y Análisis */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Panel Izquierdo - Estrategias */}
          <div className="space-y-6">
            <div className="bg-gray-900 p-6 rounded-xl shadow-md">
              <h2 className="text-xl font-semibold text-white mb-4">Estrategias Activas</h2>
              <div className="space-y-4">
                {activeStrategies.map((strategy, index) => (
                  <div key={index} className="flex justify-between items-center">
                    <div>
                      <h3 className="text-white capitalize">{strategy}</h3>
                      <p className="text-sm text-gray-400">
                        {lastAnalysis?.strategies?.[strategy]?.timeframe || '1h'} timeframe
                      </p>
                    </div>
                    <span className="text-green-400">
                      {lastAnalysis?.strategies?.[strategy]?.performance || '+0.0'}%
                    </span>
                  </div>
                ))}
              </div>
              <Link 
                href="/estrategias" 
                className="block mt-4 text-center text-cyan-400 hover:text-cyan-300 text-sm"
              >
                Ver todas las estrategias →
              </Link>
            </div>

            {/* Últimas Operaciones */}
            <div className="bg-gray-900 p-6 rounded-xl shadow-md">
              <h2 className="text-xl font-semibold text-white mb-4">Últimas Operaciones</h2>
              <div className="space-y-3">
                {lastAnalysis?.trades?.slice(0, 5).map((trade: any, index: number) => (
                  <div key={index} className="flex justify-between items-center">
                    <div>
                      <p className="text-white">{trade.pair}</p>
                      <p className="text-sm text-gray-400">
                        {trade.type} @ {trade.price}
                      </p>
                    </div>
                    <span className={trade.profit >= 0 ? "text-green-400" : "text-red-400"}>
                      {trade.profit >= 0 ? '+' : ''}{trade.profit}
                    </span>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Panel Central y Derecho */}
          <div className="lg:col-span-2 space-y-6">
            {/* Gráfico */}
            <div className="bg-gray-900 p-6 rounded-xl shadow-md">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-xl font-semibold text-white">Análisis de Mercado</h2>
                <Link 
                  href="/analisis"
                  className="text-cyan-400 hover:text-cyan-300 text-sm"
                >
                  Ver análisis completo →
                </Link>
              </div>
              <div className="h-[300px]">
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={marketData}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#374151" />
                    <XAxis 
                      dataKey="timestamp" 
                      stroke="#9CA3AF"
                      tickFormatter={(value) => new Date(value).toLocaleTimeString()}
                    />
                    <YAxis stroke="#9CA3AF" />
                    <Tooltip 
                      contentStyle={{ 
                        backgroundColor: '#1F2937',
                        border: 'none',
                        borderRadius: '0.5rem',
                        color: '#F9FAFB'
                      }}
                    />
                    <Line 
                      type="monotone" 
                      dataKey="price" 
                      stroke="#06B6D4" 
                      strokeWidth={2}
                      dot={false}
                    />
                  </LineChart>
                </ResponsiveContainer>
              </div>
            </div>

            {/* Indicadores y Estado */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {/* Predicciones IA */}
              <div className="bg-gray-900 p-6 rounded-xl shadow-md">
                <h2 className="text-xl font-semibold text-white mb-4">Predicciones IA</h2>
                <div className="space-y-3">
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Corto Plazo (24h)</span>
                    <span className={
                      lastAnalysis?.predictions?.short_term > 0 ? 'text-green-400' : 'text-red-400'
                    }>
                      {lastAnalysis?.predictions?.short_term > 0 ? '+' : ''}
                      {lastAnalysis?.predictions?.short_term?.toFixed(1) || 0}%
                    </span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Medio Plazo (7d)</span>
                    <span className={
                      lastAnalysis?.predictions?.medium_term > 0 ? 'text-green-400' : 'text-red-400'
                    }>
                      {lastAnalysis?.predictions?.medium_term > 0 ? '+' : ''}
                      {lastAnalysis?.predictions?.medium_term?.toFixed(1) || 0}%
                    </span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Confianza</span>
                    <span className="text-cyan-400">
                      {lastAnalysis?.predictions?.confidence?.toFixed(0) || 0}%
                    </span>
                  </div>
                </div>
              </div>

              {/* Estado del Sistema */}
              <div className="bg-gray-900 p-6 rounded-xl shadow-md">
                <h2 className="text-xl font-semibold text-white mb-4">Estado del Sistema</h2>
                <div className="space-y-3">
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">CPU</span>
                    <span className={`text-cyan-400 ${
                      lastAnalysis?.system?.cpu > 80 ? 'text-red-400' : ''
                    }`}>
                      {lastAnalysis?.system?.cpu || 0}%
                    </span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Memoria</span>
                    <span className={`text-cyan-400 ${
                      lastAnalysis?.system?.memory > 80 ? 'text-red-400' : ''
                    }`}>
                      {lastAnalysis?.system?.memory || 0}%
                    </span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Latencia API</span>
                    <span className={`${
                      lastAnalysis?.system?.latency < 100 ? 'text-green-400' : 'text-red-400'
                    }`}>
                      {lastAnalysis?.system?.latency || 0}ms
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
