"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { useAuth } from "@/contexts/auth-context";

export function Navigation() {
  const pathname = usePathname();
  const { user, logout } = useAuth();

  const navigation = [
    { name: "Dashboard", href: "/dashboard" },
    { name: "Trading", href: "/trading" },
    { name: "Estrategias", href: "/estrategias" },
    { name: "Análisis", href: "/analisis" },
    { name: "Configuración", href: "/configuracion" },
  ];

  return (
    <nav className="bg-gray-900 p-4">
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-8">
          <Link href="/dashboard" className="text-cyan-400 text-xl font-bold">TRADER-X AI</Link>
          <div className="hidden md:flex space-x-4">
            {navigation.map((item) => (
              <Link
                key={item.name}
                href={item.href}
                className={`${
                  pathname === item.href
                    ? "text-cyan-400"
                    : "text-gray-300 hover:text-cyan-400"
                } px-3 py-2 rounded-md text-sm font-medium transition-colors`}
              >
                {item.name}
              </Link>
            ))}
          </div>
        </div>
        <div className="flex items-center space-x-4">
          <div className="hidden md:flex items-center space-x-2">
            <span className="text-gray-300 text-sm">
              {user?.name || 'Usuario'}
            </span>
            <div className="h-6 w-px bg-gray-700"></div>
          </div>
          <Link
            href="/configuracion"
            className="text-gray-300 hover:text-cyan-400 px-3 py-2 rounded-md text-sm font-medium"
          >
            Mi Perfil
          </Link>
          <button
            onClick={logout}
            className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded-md text-sm font-medium transition-colors"
          >
            Cerrar Sesión
          </button>
        </div>
      </div>
    </nav>
  );
}
